<template>
  <div>用户名：
    <br>
    <input type="text" placeholder="请输入用户名" v-model="name">
    <br>评论内容：
    <br>
    <textarea cols="30" rows="10" placeholder="请输入评论内容" v-model="content"></textarea>
    <br>
    <button>提交</button>
  </div>
</template>

<style scoped>
div {
  width: 300px;
  font-size: 16px;
}
button {
  float: right;
}
input {
  width: 70%;
}
</style>
<script>
export default {
  data () {
    return {
      name: '',
      content: ''
    }
  }
}
</script>



