<template>
  <div id="app">
    <h1>列表---</h1>
    <add/>
    <list :com='com'/>
  </div>
</template>

<script>
import add from './components/add.vue'
import list from './components/list.vue'
export default {
  name: 'App',
  data () {
    return { com: [
      {name: 'taylor switf', content: 'blank space'},
      {name: 'charlie puth', content: 'one call away'},
      {name: 'leslie', content: 'monica'}
    ]
    }
  },
  components: {
    add,
    list
  }

}
</script>

<style>
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
</style>



